import React from 'react'
import './contect.css'
import Phone from '../../img/phone.png'
import Email from '../../img/email.png'
import Address from '../../img/address.png'
import { useRef } from 'react'
import emailjs from 'emailjs-com';
import { useState,useContext } from 'react'
import { ThemeContext } from '../../context'

const Contect = () => {
    const [name,setname]=useState("")
    const [email,setemail]=useState("")
    const [subject,setsubject]=useState("")
    const [textarea,settextarea]=useState("")
    const formRef=useRef()
    const [done,setDone]=useState(false)
    const theme=useContext(ThemeContext)
    const darkMode=theme.state.darkMode;
    const handlesubmit=(e)=>{
        if (name && email && subject && textarea){
            e.preventDefault()
            emailjs.sendForm('service_fhgdppq', 'template_mxm2gvl', formRef.current, 'FpMy0BwDGX2d1w7Fj')
            .then((result) => {
                console.log(result.text);
                setDone(true)
            }, (error) => {
                console.log(error.text);
            });
            setname("")
            setsubject("")
            setemail("")
            settextarea("")
        }
        else{
            alert("Please fill all valid information..!")
        }
       
    };
   
  return (
    <div className='c'>
        <div className="c-bg">        </div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-title">Let's Discuss About Your Projects And Idea's</h1>
                    <div className="c-info">
                        <div className="c-info-item">
                            <img src={Phone} alt="" className="c-icon" />
                            +91 9819399470
                        </div>
                        <div className="c-info-item">
                            <img src={Email} alt="" className="c-icon" />
                            aniketsshinde@protonmail.com
                        </div>
                        <div className="c-info-item">
                            <img src={Address} alt="" className="c-icon" />
                            11/14, panchasheel nagar salt pan road wadala east Mumbai-400037
                        </div>
                    </div>
                </div>
                <div className="c-right">
                    <p className="c-desc">
                        <b>What's your story?</b>Get in touch . Always freelancing if the right project comes along me.
                    </p>
                    <form ref={formRef} onSubmit={handlesubmit}>
                        <input style={{backgroundColor:darkMode && "#333"}} onChange={(e)=>setname(e.target.value)} value={name} type="text" placeholder='Name' name='user_name'  />
                        <input style={{backgroundColor:darkMode && "#333"}} onChange={(e)=>setsubject(e.target.value)}value={subject} type="text" placeholder='Subject' name='user_subject'  />
                        <input style={{backgroundColor:darkMode && "#333"}} onChange={(e)=>setemail(e.target.value)} value={email} type="text" placeholder='Email' name='user_email'  />
                        <textarea style={{backgroundColor:darkMode && "#333"}} onChange={(e)=>settextarea(e.target.value)}value={textarea} rows="5" placeholder='Message' name='message'></textarea>
                        <button >Submit</button>
                        {done && "Thank you! your form has been submitted...!"}
                    </form>
                </div>
            </div>
    </div>
  )
  };

export default Contect;